import React from "react"
import Img from "gatsby-image"
import { Link } from "gatsby"

import Layout from "./layout"
import SEO from "./seo"
import DateAndTags from "./date_and_tags"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faAngleDoubleDown } from "@fortawesome/free-solid-svg-icons"

export default ({ data, pageContext, location }) => {
  return (
    <Layout>
      <SEO
        title={pageContext.pageTitle || ""}
        pagePath={location.pathname}
      />
      {pageContext.pageTitle &&
        <h1 className="title">{pageContext.pageTitle}</h1>
      }
      {data.allMarkdownRemark.edges.map(({ node }) => (
        <article className="card mb-6">
          <header>
            <div className="card-content pb-0">
              <DateAndTags
                date={node.fields.jstDate}
                tags={node.frontmatter.tags}
              />
              <h1 className="title mb-4">
                <Link to={node.fields.urlPath}>
                  {node.frontmatter.title}
                </Link>
              </h1>
            </div>
            {node.frontmatter.eyecatch && (
              <Link to={node.fields.urlPath} className="card-image mb-4">
                <figure className="image">
                  <Img fluid={node.frontmatter.eyecatch.childImageSharp.fluid} alt={`eyecatch image of ${node.frontmatter.title}`} />
                </figure>
              </Link>
            )}
          </header>
          <div className="card-content pt-0">
            <div dangerouslySetInnerHTML={{ __html: node.excerpt }} className="content" />
            <Link to={node.fields.urlPath}>
              <div className="has-text-centered">
                <button class="button">
                  <span className="icon mr-1"><FontAwesomeIcon icon={faAngleDoubleDown} /></span>
                  more
                </button>
              </div>
            </Link>
          </div>
        </article>
      ))}
      <nav className="pagination is-centered" role="navigation" aria-label="pagination">
        {!pageContext.isFirstPage &&
          <Link to={pageContext.previousPagePath} className="pagination-previous">
            Newer
          </Link>
        }
        <ul className="pagination-list">
          {!pageContext.isFirstPage &&
            <li><Link to={pageContext.firstPagePath} className="pagination-link" aria-label="Goto Page 1">1</Link></li>
          }
          {pageContext.currentPage > 3 &&
            <li><span class="pagination-ellipsis">&hellip;</span></li>
          }
          {pageContext.currentPage > 2 &&
            <li><Link to={pageContext.previousPagePath} className="pagination-link" aria-label={`Goto Page ${pageContext.currentPage - 1}`}>{pageContext.currentPage - 1}</Link></li>
          }
          <li><Link to={pageContext.currentPagePath} className="pagination-link is-current" aria-label={`Page ${pageContext.currentPage}`} area-current="page">{pageContext.currentPage}</Link></li>
          {pageContext.totalBlogsPages - pageContext.currentPage > 1 &&
            <li><Link to={pageContext.nextPagePath} className="pagination-link" aria-label={`Goto Page ${pageContext.currentPage + 1}`}>{pageContext.currentPage + 1}</Link></li>
          }
          {pageContext.totalBlogsPages - pageContext.currentPage > 2 &&
            <li><span class="pagination-ellipsis">&hellip;</span></li>
          }
          {!pageContext.isLastPage &&
            <li><Link to={pageContext.lastPagePath} className="pagination-link" aria-label={`Goto Page ${pageContext.totalBlogsPages}`}>{pageContext.totalBlogsPages}</Link></li>
          }
        </ul>
        {!pageContext.isLastPage &&
          <Link to={pageContext.nextPagePath} className="pagination-next">
            Older
          </Link>
        }
      </nav>
    </Layout >
  )
}
